$grid-max-col-count: 3;
$grid-min-col-size: 28rem;
$grid-gap: 1rem;

$grid-col-size-calc: calc(
  (100% - $grid-gap * $grid-max-col-count) / $grid-max-col-count
);

$grid-col-min-size-calc: min(
  100%,
  max($grid-min-col-size, $grid-col-size-calc)
);

.grid {
  display: grid;
  gap: $grid-gap;
  grid-template-columns: repeat(auto-fit, minmax($grid-col-min-size-calc, 1fr));

  &-item {
    border: 1px solid var(--color-10);
    padding: 1rem;
  }
}
